import { Component, OnInit, ViewChild } from '@angular/core';
import { DataTableDirective } from 'angular-datatables';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { Subject } from 'rxjs';
import { ITADCompanyList } from '../../_models/ITADCompanyList';
import { ITADSupliers } from '../../_models/ITADSupliers';
import { AlertifyService } from '../../_services/alertify.service';
import { AuthService } from '../../_services/auth.service';
import { LoadspecsService } from '../../_services/loadspecs.service';

@Component({
  selector: 'app-itad-company-list',
  templateUrl: './itad-company-list.component.html',
  styleUrls: ['./itad-company-list.component.css']
})
export class ItadCompanyListComponent implements OnInit {
  users: ITADSupliers[];
  model: any = {};
  resetmodel: any = {};
  itadcomlists: ITADCompanyList[];
  @ViewChild('supplierlist') public supplierlist: ModalDirective;

  @ViewChild(DataTableDirective) dtElement: DataTableDirective;
  dtOptions: any = {
    pagingType: 'full_numbers',
    pageLength: 25,
    retrieve: true,
    order: [[0, 'desc']]
  };
dtInstance: DataTables.Api;
isDtInitialized: boolean = false;
dtTrigger: Subject<ITADCompanyList> = new Subject();

  constructor(public authService: AuthService, public loadspecsService: LoadspecsService, public alertify: AlertifyService) { }

  ngOnInit(): void {
    this.loadITADCompanyLists();
  }
  loadITADCompanyLists() {
    return this.loadspecsService.getITADCompanyList().subscribe((itadcomlists: ITADCompanyList[]) => {
      this.itadcomlists = itadcomlists;
      if (this.isDtInitialized) {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
          dtInstance.destroy();
          this.dtTrigger.next();
        });
      } else {
        this.isDtInitialized = true;
        this.dtTrigger.next();
      }
    }, error => {
      this.alertify.error(error);
    });
  }

  ApproveCompany(id: number) {
    this.model = {id : id};
    document.getElementById('postitad_' + id).innerHTML = '<i class="fa fa-circle-o-notch mt-1 fa-lg fa-spin"></i> approving...';
    (document.getElementById('postitad_' + id) as HTMLButtonElement).disabled = true;
    this.loadspecsService.ApproveCompany(this.model).subscribe(() => {
      this.model.model = this.model;
          this.loadITADCompanyLists();
          document.getElementById('postitad_' + id).innerHTML = 'Submit';
          (document.getElementById('postitad_' + id) as HTMLButtonElement).disabled = false;
          this.alertify.success('Company approved Successfully');
      }, error => {
        this.alertify.error(error);
      });
 }

 ViewCompanyDetails(id: number) {
   return this.loadspecsService.getITADSuppliers().subscribe((users: ITADSupliers[]) => {
    this.users = users.filter(proj => (Number(proj.companyId) === id));
    if (this.users.length > 0) {
      this.supplierlist.show();
    } else {
      this.alertify.error('No Suppliers Registered Yet');
    }
  }, error => {
    this.alertify.error(error);
  });
 }

 RejectCompany(id: number) {
  this.model = {id : id};
  document.getElementById('postitad1_' + id).innerHTML = '<i class="fa fa-circle-o-notch mt-1 fa-lg fa-spin"></i> Rejecting...';
  (document.getElementById('postitad1_' + id) as HTMLButtonElement).disabled = true;
  this.loadspecsService.RejectCompany(this.model).subscribe(() => {
    this.model.model = this.model;
        this.loadITADCompanyLists();
        document.getElementById('postitad_' + id).innerHTML = 'Submit';
        (document.getElementById('postitad_' + id) as HTMLButtonElement).disabled = false;
        this.alertify.success('Company Rejected Successfully');
    }, error => {
      this.alertify.error(error);
    });
}

ActivateUser(id: any) {
  this.resetmodel.id = id;
  document.getElementById('postitad_' + id).innerHTML = '<i class="fa fa-circle-o-notch mt-1 fa-lg fa-spin"></i> approving...';
  (document.getElementById('postitad_' + id) as HTMLButtonElement).disabled = true;
  this.authService.activateuser(this.resetmodel).subscribe(() => {
    document.getElementById('postitad_' + id).innerHTML = 'Submit';
    (document.getElementById('postitad_' + id) as HTMLButtonElement).disabled = false;
    // this.ViewCompanyDetails();
    this.alertify.success('Successfully Updated');
  }, error => {
    if (error.error.Password !== undefined) {
      this.alertify.error(error.error.Password[0]);
    } else {
      this.alertify.error(error.error);
    }
  });
}

}
